<template>
	<view class="container">
		<form @submit="bindSave" :reportSubmit="true">
			<view class="tipTitle">选择服务信息</view>
			<view class="addressInfo">
				<view @tap="addAddress" class="addresss">
					<view v-if="addresses.length == 0">选择服务地址</view>
					<view class="addressContent" v-else>
						<view>
							{{ name }}
							<text class="phone">{{ phone }}</text>
						</view>
						<view class="doorNumber">{{ community }}{{ door_number }}</view>
					</view>
				</view>
				<view class="dateCon">
					<picker @change="bindMultiPickerChange" @columnchange="bindMultiPickerColumnChange" mode="multiSelector" :range="multiArray" :value="multiIndex">
						<view class="picker">{{ multiArray[0][multiIndex[0]] }} {{ multiArray[1][multiIndex[1]] }}</view>
					</picker>
				</view>
			</view>
			<view class="tipTitle">选择套餐</view>
			<view class="setMeals">
				<picker @change="bindPickerChange" :range="mealArray" rangeKey="namePrice" :value="mealIndex">
					<view class="picker">{{ mealArray[mealIndex].namePrice }}</view>
				</picker>
			</view>
			<view class="tipTitle">备注</view>
			<view class="markCon"><textarea name="memo" placeholder="川湘鲁粤苏闽浙徽吃哪个?酸甜苦辣冷热咸香好哪口?" :value="memo"></textarea></view>
			<view class="fixFoot">
				<view class="tips">—— 稍后将有专员联系 ——</view>
				<button class="saveBtn" formType="submit" type="warn">提交</button>
			</view>
		</form>
	</view>
</template>

<script>
var app = getApp();

/**
 * 向厨师订餐
 */
export default {
	data() {
		return {
			multiArray: [],
			multiIndex: [0, 0],
			mealArray: [],
			mealIndex: 0,
			community: null,
			door_number: null,
			name: null,
			phone: null,
			memo: null,
			package_type: '1',
			addresses: []
		};
	},
	onShow: function() {
		this.getBespeakTime();
		this.getInfoDetail();
	},
	methods: {
		packages: {},
		package_prices: {},
		work_days: {},

		getBespeakTime: function() {
			var that = this;
			uni.request({
				url: app.globalData.baseUrl.url + app.globalData.order.bespeakTime,
				data: null,
				header: {
					Cookie: 'auth=' + app.globalData.cookieAuth + '; city_id=' + app.globalData.cityId
				},
				success: function(e) {
					if (10000 == e.data.code) {
						that.packages = e.data.data.quick.packages;
						that.package_prices = e.data.data.quick.package_prices;
						that.work_days = e.data.data.quick.work_days;
						var i = [[], []];

						for (var s in that.package_prices) {
							i[0].push(s);
						}

						that.setData({
							multiArray: i
						});
						that.resetTime();
					} else {
						if (401 == e.data.code) {
							return void uni.navigateTo({
								url: '/pages/login/login'
							});
						}

						app.globalData.showToast(e.data.error);
					}
				}
			});
		},

		bindMultiPickerChange: function(a) {
			this.setData({
				multiIndex: a.detail.value
			});
		},

		bindMultiPickerColumnChange: function(a) {
			var t = {
				multiArray: this.multiArray,
				multiIndex: this.multiIndex
			};

			switch (((t.multiIndex[a.detail.column] = a.detail.value), a.detail.column)) {
				case 0:
					this.resetTime();
					break;

				case 1:
					this.resetPrice();
			}

			this.setData(t);
		},

		resetPrice: function() {
			var a = this.multiArray[0][this.multiIndex[0]];
			var t = this.multiArray[1][this.multiIndex[1]];
			var e = [];
			for (var i in this.packages[a]) {
				e.push({
					id: this.packages[a][i].id,
					date: a + ' ' + t,
					namePrice: this.packages[a][i].name + '/' + this.package_prices[a][t][this.packages[a][i].id] + '元'
				});
			}

			e[this.mealIndex] || (this.mealIndex = 0);
			this.setData({
				mealArray: e,
				mealIndex: this.mealIndex
			});
		},

		resetTime: function() {
			var a = [];
			var t = this.multiArray[0][this.multiIndex[0]];
			var e = this.multiArray[1][this.multiIndex[1]];
			a = this.work_days[t];
			this.multiArray[1] = a;
			this.package_prices[t][e] || (this.multiIndex[1] = 0);
			this.setData({
				multiArray: this.multiArray,
				multiIndex: this.multiIndex
			});
			this.resetPrice();
		},

		bindPickerChange: function(a) {
			this.setData({
				mealIndex: a.detail.value
			});
		},

		bindSave: function(t) {
			console.log(t);
			var that = this;
			var i = this.community;
			var s = this.door_number;
			var d = this.name;
			var r = this.phone;
			var n = t.detail.value.memo;
			var o = /^((0\d{2,3}-?\d{7,8})|(1[35784]\d{9}))$/;
			if ('' != i) {
				if ('' != s) {
					if ('' != d) {
						if (o.test(r)) {
							var l = this.multiArray[0][this.multiIndex[0]] + ' ' + this.multiArray[1][this.multiIndex[1]];
							var u = this.mealArray[this.mealIndex].id;
							uni.showLoading();
							uni.request({
								url: app.globalData.baseUrl.url + app.globalData.order.saveOrder,
								data: {
									date: l,
									package_type: that.package_type,
									package: u,
									community: i,
									door_number: s,
									name: d,
									phone: r,
									memo: n,
									app: 13
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded',
									Cookie: 'auth=' + app.globalData.cookieAuth + '; city_id=' + app.globalData.cityId
								},
								method: 'POST',
								success: function(t) {
									if (10000 == t.data.code) {
										uni.navigateTo({
											url: '/pages/placeOrder/placeOrder?id=' + t.data.data.id
										});
									} else {
										if (401 == t.data.code) {
											uni.hideLoading();
											uni.showModal({
												title: '提示',
												content: t.data.error,
												success: function(a) {
													if (a.confirm) {
														uni.switchTab({
															url: '/pages/my/my'
														});
													}
												}
											});
										} else {
											uni.hideLoading();
											app.globalData.showToast(t.data.error);
										}
									}
								}
							});
						} else {
							this.prompt('请选择地址');
						}
					} else {
						this.prompt('请选择地址');
					}
				} else {
					this.prompt('请选择地址');
				}
			} else {
				this.prompt('请选择地址');
			}
		},

		getInfoDetail: function() {
			var that = this;
			uni.request({
				url: app.globalData.baseUrl.url + app.globalData.user.info,
				data: null,
				header: {
					Cookie: 'auth=' + app.globalData.cookieAuth + '; city_id=' + app.globalData.cityId
				},
				success: function(a) {
					if (10000 == a.data.code) {
						if (0 != a.data.data.addresses.length) {
							that.setData({
								addresses: a.data.data.addresses,
								community: a.data.data.addresses[0].community,
								door_number: a.data.data.addresses[0].door_number,
								name: a.data.data.addresses[0].name,
								phone: a.data.data.addresses[0].phone
							});
						} else {
							that.setData({
								addresses: a.data.data.addresses
							});
						}
					} else {
						a.data.code;
						that.setData({
							addresses: []
						});
					}
				}
			});
		},

		addAddress: function() {
			uni.navigateTo({
				url: '/pages/addressList/addressList?pages=4'
			});
		},

		prompt: function(a) {
			uni.showModal({
				title: '提示',
				content: a,
				showCancel: false,
				confirmText: '知道了',
				success: function(a) {}
			});
		}
	}
};
</script>
<style>
page {
	height: 100%;
}

.container,
page {
	background-color: #f5f5f9;
}

.container {
	justify-content: normal;
}

.tipTitle {
	font-size: 24rpx;
	color: #989898;
	text-align: center;
	margin: 20rpx 0;
}

.addressInfo {
	display: flex;
	margin: 0 30rpx;
	align-items: center;
}

.addresss {
	height: 120rpx;
	display: flex;
	align-items: center;
	justify-content: center;
}

.addresss,
.dateCon {
	flex: 1;
	text-align: center;
	color: red;
	font-size: 26rpx;
	background-color: #fff;
	border-radius: 6px;
}

.dateCon {
	line-height: 120rpx;
}

.addressContent .phone,
.dateCon {
	margin-left: 20rpx;
}

.addressContent .doorNumber {
	margin-top: 10rpx;
}

.setMeals {
	border-radius: 6px;
	line-height: 150rpx;
	text-align: center;
	font-size: 28rpx;
}

.markCon,
.setMeals {
	background-color: #fff;
	margin: 0 30rpx;
}

.form-box {
	width: 100%;
	background-color: #fff;
	margin-bottom: 10rpx;
}

.row-wrap {
	height: 88rpx;
	line-height: 88rpx;
}

.row-wrap,
.row-wrap2 {
	width: 720rpx;
	padding-left: 30rpx;
	border-bottom: 1rpx solid #eee;
	display: flex;
	font-size: 28rpx;
}

.row-wrap2 {
	min-height: 88rpx;
}

.bg_f5 {
	background-color: #f5f5f5;
	color: #e64340;
}

.row-wrap .label {
	width: 160rpx;
	color: #000;
}

.row-wrap .label-right {
	flex: 1;
	height: 88rpx;
	line-height: 88rpx;
}

.row-wrap .label-right input {
	height: 100%;
	font-size: 28rpx;
	padding-right: 30rpx;
}

.row-wrap .right-box {
	margin-right: 30rpx;
}

.cancel-btn,
.save-btn {
	width: 690rpx;
	height: 80rpx;
	line-height: 80rpx;
	text-align: center;
	margin-top: 30rpx;
	border-radius: 6rpx;
	box-sizing: border-box;
}

.save-btn {
	background-color: #e64340;
	color: #fff;
	margin-bottom: 80rpx;
}

button[type='default'] {
	background-color: #fff;
	color: #000;
}

picker {
	height: 100%;
	flex: 1;
}

textarea {
	width: 630rpx;
	height: 150rpx;
	font-size: 28rpx;
	color: red;
	padding: 30rpx 30rpx 20rpx;
}

.fixFoot {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
}

.fixFoot .tips {
	text-align: center;
	font-size: 26rpx;
	color: #989898;
	margin-bottom: 5rpx;
}

.fixFoot .saveBtn {
	border-radius: 0;
	background-color: red;
}
</style>
